/* LINKS */

@mixin underline {
	text-decoration: none;
	border-bottom: 1px solid;
}

@mixin noUnderline {
	text-decoration: none;
	border-bottom: none;
}

@mixin hideUnderline {
	text-decoration: none;
	border-bottom: 1px solid transparent;
}

a {
	color: inherit;
	@include underline;
	transition: color 400ms;

	&:hover,
	&:focus,
	&:active {
		color: getColor(hemelblauw default);
	}
}

.interfaceLink {
	$lineHeight: 1.6;

	line-height: $lineHeight;
	@include noUnderline;
	color: getColor(hemelblauw default);
	fill: getColor(hemelblauw default);
	display: inline-flex;
    align-items: flex-start;


	@include hoverEtc {
		.interfaceLink-text {
			@include underline;
		}
	}

	&-icon {
		flex: 0 0 auto;
		height: ($lineHeight * 1em);
		margin-right: 0.3em;
	}
	&-text {
		flex: 1 1 0;
		@include hideUnderline;
	}

}

.headingLink {
	$lineHeight: 1.2;

	line-height: $lineHeight;
	@include noUnderline;
	color: getColor(hemelblauw default);
	fill: getColor(hemelblauw default);

	@include hoverEtc {
		.interfaceLink-text {
			@include underline;
		}
	}

	&-icon {
		display: inline-block;
		height: ($lineHeight * 1em);
		margin-left: 0.3em;
		vertical-align: middle;
	}
	&-text {
		@include hideUnderline;
	}
}

.externalLink {
	$lineHeight: 1.2;

	line-height: $lineHeight;
	@include noUnderline;

	@include hoverEtc {
		.externalLink-text {
			@include underline;
		}
	}

	&-icon {
		height: ($lineHeight * 1em);
		//height: 0.75em;
		//margin-left: 0.15em;
		vertical-align: middle;
	}
	&-text {
		@include underline;
	}
}